html {
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
}

body {
  font-family: "opensans-regular", sans-serif;
  font-weight: normal;
  font-size: 25px;
  line-height: 30px;
  color: #000000;

  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
