.row {
  /* width: 96%;
   max-width: auto;
   margin: 20px auto; */
  --bs-gutter-x: 1.5rem;
  --bs-gutter-y: 0;
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(-1 * var(--bs-gutter-y));
  margin-right: calc(-0.5 * var(--bs-gutter-x));
  margin-left: calc(-0.5 * var(--bs-gutter-x));
  justify-content: center;
}

.ie .row {
  width: 1000px;
}

.narrow .row {
  max-width: 980px;
}

.row .row {
  width: auto;
  max-width: none;
  margin: 0 -20px;
}

.row:before,
.row:after {
  content: " ";
  display: table;
}
.row:after {
  clear: both;
}

.column,
.columns {
  /* position: relative;
  padding: 0 20px;
  min-height: 1px;
  float: left; */
  float: left;
  width: 33.3%;
  margin-bottom: 16px;
  padding: 0 8px;
}

.card {
  box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.2);
  margin: 8px;
  border-radius: 12px;
  background: #acaaaa;
}

.projects,
.card-1 {
  box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.2);
  margin: 8px;
  border-radius: 12px;
  background: #acaaaa;
}

.container {
  padding: 0 16px;
}

.container::after,
.row::after {
  content: "";
  clear: both;
  display: table;
}

.grow img {
  transition: 1s ease;
  border-radius: 12px;
}

.grow img:hover {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
  transition: 2s ease;
  border-radius: 50px 300px 50px 300px;
}

.column.centered,
.columns.centered {
  float: none;
  margin: 0 auto;
}

.row.collapsed > .column,
.row.collapsed > .columns,
.column.collapsed,
.columns.collapsed {
  padding: 0;
}

